<template>
  <div class="This">
    <h3>this指向问题</h3>
    <li>一般情况this指向调用他的对象</li>
    <pre>
    1.全局作用域或者普通函数中this指向全局对象window 因为他们都属于window. (定时器也是window. 里面的this也属于window)
    
    console.log(this); //window

    function fun() {
      console.log(this);
    }
    fun(); //window

    setTimeout(function () {
      console.log(this);
    }, 1000); //window

    2.方法调用中指向调用者

    var o = {
     sahi:function(){
       console.log(this);
     }
   }
   o.sahi(); //指向o

   button.onclick = function(){
     console.log(this); //指向button
   }

   3.构造函数中this指向new对象者
   
    function Fun() {
      console.log(this);
    }
    var fun = new Fun();
    //指向fun
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  computed: {},
  mounted() {
    
  },
};
</script>

