import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home'
import Welcome from '../components/Welcome'
import Get from '../components/Get'
import Set from '../components/Set'
import Setstyle from '../components/Setstyle'
import Intdemo from '../components/Intdemo'
import Nodedemo from '../components/Nodedemo'
import Domdemo from '../components/Domdemo'
import Shijianliu from '../components/Shijianliu'
import Buchong from '../components/Buchong'
import Changyong from '../components/Changyong'
import Bomdemo from '../components/Bomdemo'
import Timeout from '../components/Timeout'
import This from '../components/This'
import Duilie from '../components/Duilie'
import Location from '../components/Location'
import Qitabom from '../components/Qitabom'
import Dffects from '../components/advanced/Effects'
import Client from '../components/advanced/Client'
import Scroll from '../components/advanced/Scroll'
import Animation from '../components/advanced/Animation'
import Carousel from '../components/advanced/Carousel'
import Anli2 from '../components/advanced/Anli2'
import Local from '../components/advanced/Local'



Vue.use(VueRouter)

const routes = [
  {
    path: '',
    redirect: '/home'

  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: "", redirect: '/welcome' },
      { path: "/welcome", component: Welcome },
      { path: "/get", component: Get },
      { path: "/set", component: Set },
      { path: "/setstyle", component: Setstyle },
      { path: "/intdemo", component: Intdemo },
      { path: "/nodedemo", component: Nodedemo },
      { path: "/domdemo", component: Domdemo },
      { path: "/shijianliu", component: Shijianliu },
      { path: "/buchong", component: Buchong },
      { path: "/changyong", component: Changyong },
      { path: "/bomdemo", component: Bomdemo },
      { path: "/timeout", component: Timeout },
      { path: "/this", component: This },
      { path: "/duilie", component: Duilie },
      { path: "/location", component: Location },
      { path: "/qitabom", component: Qitabom },
      { path: "/dffects", component: Dffects },
      { path: "/client", component: Client },
      { path: "/scroll", component: Scroll },
      { path: "/animation", component: Animation },
      { path: "/carousel", component: Carousel },
      { path: "/anli2", component: Anli2 },
      { path: "/local", component: Local },
    ]
  }
]

const router = new VueRouter({
  /* mode: 'history', */
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
