<template>
  <div class="Nodedemo">
    <h2>节点操作可以更容易的获取/操作元素</h2>
    <p>节点拥有nodeType(节点类型) nodeName(节点名) nodeValue(节点值)</p>
    <p>nodeType 1是元素节点 2是属性节点 3是文本节点(包括文字空格换行)</p>
    <br />
    <h3>获取父节点</h3>
    <li>parentNode</li>
    <div>父节点 <span class="zjd">子节点</span></div>
    <pre>
      var a = document.querySelector(".zjd");
      var b = a.parentNode;
      console.log(b);//获取到了父节点
      <button @click="fjd">点我试试</button></pre>
    <h3>获取子节点</h3>
    <li>childNodes</li>
    <div class="fjd">父节点 <span>子节点</span></div>
    <pre>
      var a = document.querySelector(".fjd");
      var b = a.childNodes;
      console.log(b); //获取到了子节点//空格换行也是子节点
      <button @click="zjd">点我试试</button></pre>
    <h3>获取子节点(好用)</h3>
    <li>children</li>
    <div class="fjd">父节点 <span>子节点</span></div>
    <pre>
      var a = document.querySelector(".fjd");
      var b = a.children;
      console.log(b); //获取到了子节点
      <button @click="zjdnice">点我试试</button> </pre>
    <h3>获取第一个/最后一个子节点</h3>
    <li>firstChild</li>
    <li>lastChild</li>
    <div class="first">
      1
      <div></div>
      <span></span>
      <a href=""></a>
    </div>
    <pre>    var a = document.querySelector(".first");
      console.log(a.firstChild);
      console.log(a.lastChild);
      <button @click="first">点我试试</button></pre>
    <h3>获取第一个/最后一个子节点(好用)</h3>
    <li>firstElementChild</li>
    <li>lastElementChild</li>
    <pre>    var a = document.querySelector(".first");
      console.log(a.firstElementChild);
      console.log(a.lastElementChild);
      <button @click="firstnice">点我试试</button></pre>
    <h3>没有兼容性问题的写法</h3>
    <pre>
      var a = document.querySelector(".first");
      console.log(a.children[0]);
      console.log(a.children[a.children.length-1]);
      <button @click="jrx">点我试试</button></pre>
    <h3>新浪下拉案例</h3>
    <ul class="xiala">
      <li>
        <div>触发</div>
        <ul>
          <li>第一</li>
          <li>第二</li>
        </ul>
      </li>
      <li>
        <div>触发</div>
        <ul>
          <li>第一</li>
          <li>第二</li>
        </ul>
      </li>
    </ul>
    <pre>
      var a = document.querySelector(".xiala");
      var b = a.children;
      for (var i = 0; i < b.length; i++) {
        b[i].onmouseover = function () {
          this.children[1].style.display = "block";
        };
        b[i].onmouseout = function () {
          this.children[1].style.display = "none";
        };
      }
      <button @click="xiala">点我试试</button></pre>
    <h3>获取下一个兄弟节点</h3>
    <li>nextSibling</li>
    <div class="xiongdi"></div>
    1
    <span></span>
    <pre>
      var a = document.querySelector(".xiongdi");
      console.log(a.nextSibling);//文本换行也算
      <button @click="xiongdi">点我试试</button></pre>
    <h3>获取下一个兄弟节点(好用)</h3>
    <li>nextElementSibling</li>
    <pre>
       var a = document.querySelector(".xiongdi");
       console.log(a.nextElementSibling); 
      <button @click="xiongdinice">点我试试</button></pre>
    <h3>获取上一个兄弟节点</h3>
    <li>previousSibling</li>
    <div></div>
    1
    <span class="sygxiongdi"></span>
    <pre>
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousSibling);//文本换行也算
      <button @click="sygxiongdi">点我试试</button>
    </pre>
    <h3>获取上一个兄弟节点(好用)</h3>
    <li>previousElementSibling</li>
    <pre>
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousElementSibling); 
      <button @click="sygxiongdinice">点我试试</button></pre>
    <h3>解决兼容性问题</h3>
    <p>自己封装函数</p>
    <pre>
  function getNextElementsibling(element) {
      var el = element;
      while ((el = el.nextSibling)) {
        if (el.nodeType === 1) {
          return el;
        }
      }
      return null;
    }</pre
    >
    <h3>创建添加节点(尾部)</h3>
    <li>appendChild(追加到末尾)</li>
    <ul class="addnode">
      1
    </ul>
    <pre>    //创建节点
      var a = document.createElement("li");
      //添加到页面
      var b = document.querySelector(".addnode");
      b.appendChild(a);
      <button @click="addnode">点我试试</button></pre>
    <h3>创建添加节点(指定位置)</h3>
    <li>insertBefore(追加到指定位置)</li>
    <pre>    //创建节点
      var a = document.createElement("li");
      //插入到元素指定位置
      var b = document.querySelector(".addnode");
      b.insertBefore(a, b.children[0]);
      <button @click="befonode">点我试试</button></pre>
    <h3>留言板案例</h3>
    <div>
      <input type="text" name="" id="wenben" /><button id="fabu">发布</button>
      <ul id="ulneir">
        <li>123</li>
      </ul>
    </div>
    <pre>
      var ipt = document.querySelector("#wenben");
      var btn = document.querySelector("#fabu");
      var ul = document.querySelector("#ulneir");
      btn.onclick = function () {
        var a = document.createElement("li");
        a.innerHTML = ipt.value;
        ul.appendChild(a);
      };</pre
    >
    <h3>删除节点</h3>
    <li>removeChild</li>
    <ul class="ulxiongda">
      <li>熊大</li>
      <li>熊二</li>
    </ul>
    <pre>
      var a = document.querySelector(".ulxiongda");
      console.log(a);
      if (a.children.length == 0) return alert("没有了");
      a.removeChild(a.children[0]);
      <button @click="delli">点我试试</button></pre>
    <h3>留言板删除案例</h3>
    <div>
      <input type="text" name="" id="wenben2" /><button id="fabu2">发布</button>
      <ul id="ulneir2"></ul>
    </div>
    <pre>
      var ipt = document.querySelector("#wenben2");
      var btn = document.querySelector("#fabu2");
      var ul = document.querySelector("#ulneir2");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("你没有输入内容");
          return;
        }
        a.innerHTML = ipt.value + "<a href='javascript:;'>删除</a>";
        ul.appendChild(a);
        //删除元素
        var b = ul.querySelectorAll("a");
        for (var i = 0; i < b.length; i++) {
          b[i].onclick = function () {
            ul.removeChild(this.parentNode);
          };
        }
      };
      </pre>
    <h3>复制节点</h3>
    <li>cloneNode(true/false)</li>
    <p>false浅拷贝,只拷贝标签不拷贝内容</p>
    <ul class="clone">
      <li>你好</li>
    </ul>
    <pre>    var a = document.querySelector(".clone");
      var b = a.children[0].cloneNode(true);
      a.appendChild(b);
      <button @click="clone">点我试试</button></pre>
    <h3>创建元素的区别</h3>
    <ul>
      <li>document.write 一般不使用，会重构页面</li>
      <li>innerHTML 是把内容写入到某个DOM节点，不会重绘页面</li>
      <li>
        innerHTML 创建多个元素效率最高
        但是！！不要使用字符串拼接！要使用数组形式拼接
      </li>
      <li>createElement 创建多个元素效率低一点点 但是结构清晰</li>
    </ul>
      <pre>
    //这里的百度被解析了，其实是a链接
    //效率低
    var inner = document.querySelector(".inner");
    inner.innerHTML += "<a href='#'>百度</a>";
    //效率还不错
    var create = document.querySelector(".create");
    var a = document.createElement("a");
    create.appendChild("a");
    //效率最高
    var arr = [];
    arr.push("<a href='#'>百度</a>");
    inner.innerHTML = arr.join("");
      </pre>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    fjd() {
      var a = document.querySelector(".zjd");
      var b = a.parentNode;
      console.log(b); //获取到了父节点
    },
    zjd() {
      var a = document.querySelector(".fjd");
      var b = a.childNodes;
      console.log(b); //获取到了子节点//空格换行也是子节点
    },
    zjdnice() {
      var a = document.querySelector(".fjd");
      var b = a.children;
      console.log(b); //获取到了子节点
    },
    first() {
      var a = document.querySelector(".first");
      console.log(a.firstChild);
      console.log(a.lastChild);
    },
    firstnice() {
      var a = document.querySelector(".first");
      console.log(a.firstElementChild);
      console.log(a.lastElementChild);
    },
    jrx() {
      var a = document.querySelector(".first");
      console.log(a.children[0]);
      console.log(a.children[a.children.length - 1]);
    },
    xiala() {
      var a = document.querySelector(".xiala");
      var b = a.children;
      for (var i = 0; i < b.length; i++) {
        b[i].onmouseover = function () {
          this.children[1].style.display = "block";
        };
        b[i].onmouseout = function () {
          this.children[1].style.display = "none";
        };
      }
    },
    xiongdi() {
      var a = document.querySelector(".xiongdi");
      console.log(a.nextSibling); //文本换行也算
    },
    xiongdinice() {
      var a = document.querySelector(".xiongdi");
      console.log(a.nextElementSibling);
    },
    sygxiongdi() {
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousSibling); //文本换行也算
    },
    sygxiongdinice() {
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousElementSibling);
    },
    addnode() {
      //创建节点
      var a = document.createElement("li");
      //添加到页面
      var b = document.querySelector(".addnode");
      b.appendChild(a);
    },
    befonode() {
      //创建节点
      var a = document.createElement("li");
      //插入到元素指定位置
      var b = document.querySelector(".addnode");
      b.insertBefore(a, b.children[0]);
    },
    liuyan() {
      var ipt = document.querySelector("#wenben");
      var btn = document.querySelector("#fabu");
      var ul = document.querySelector("#ulneir");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("你没有输入内容");
          return;
        }
        a.innerHTML = ipt.value;
        ul.appendChild(a);
      };
    },
    delli() {
      var a = document.querySelector(".ulxiongda");
      console.log(a);
      if (a.children.length == 0) return alert("没有了");
      a.removeChild(a.children[0]);
    },
    dellyb() {
      var ipt = document.querySelector("#wenben2");
      var btn = document.querySelector("#fabu2");
      var ul = document.querySelector("#ulneir2");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("你没有输入内容");
          return;
        }
        a.innerHTML = ipt.value + "<a href='javascript:;'>删除</a>";
        ul.appendChild(a);
        //删除元素
        var b = ul.querySelectorAll("a");
        for (var i = 0; i < b.length; i++) {
          b[i].onclick = function () {
            ul.removeChild(this.parentNode);
          };
        }
      };
    },
    clone() {
      var a = document.querySelector(".clone");
      var b = a.children[0].cloneNode(true);
      a.appendChild(b);
    },
  },
  mounted() {
    this.liuyan();
    this.dellyb();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.xiala {
  overflow: hidden;
  list-style: none;
  > li {
    list-style: none;
    float: left;
    margin-left: 50px;
    ul,
    li {
      padding: 0;
      list-style: none;
    }
    ul {
      display: none;
    }
  }
}
</style>