<template>
  <div class="Buchong">
    <h3>立即执行函数</h3>
    <p>(function(){})(e)</p>
    <p>(function(){}(e))</p>
    <pre>
      //立即执行不需要调用
      
      //创建了一个单独的作用域
    </pre>
    <h3>devicePixelRatio</h3>
    <pre>
    //物理像素比 通常电脑是1 其他设备
    //通常用来设置字体或者用来rem

    //获取物理像素比
    var dpr = window.devicePixelRatio;
    //设置body字体大小
    document.body.style.fontSize = 12 * dpr + "px";
    //设置rem大小  //html的宽度划分10份
    var rem = document.documentElement.clientWidth / 10;
    //设置html中的字体大小是10等份之一
    document.documentElement.style.fontSize = rem + "px";

    //把设置字体封装函数 可以在页面大小改变时重新设置
    </pre>
    <h3>其他事件</h3>
    <ul>
      <li>pageshow  //重新进入页面事件</li>
      <li>load  //重新加载页面事件</li>
    </ul>
    <hr>
    <h3>两个鼠标移入事件的区别</h3>
    <pre>
      //mouseenter
      //mouseover
      //mouseover移入触发,移入子盒子还会触发 也就是会冒泡
      //mouseenter 只会移入自身触发 不会冒泡
    </pre>
    <h3>常用组件库</h3>
    <li>swiper 轮播</li>
    <li>zy.media.js 视频</li>
    <li>bootstrap</li>
    <li>superslide</li>
    <li>iscroll</li>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  computed: {},
  mounted() {},
};
</script>

