<template>
  <div class="Buchong">
    <h3>常用事件</h3>
    <pre><img src="../assets/on.png" ></pre>
    <h3>补充</h3>
    <pre>
    //阻止右键菜单
    document.addEventListener("contextmenu", function (e) {
      e.preventDefault();
    });
    //阻止选中文字
    document.addEventListener("selectstart", function (e) {
      e.preventDefault();
    });
    </pre>
    <h3>补充</h3>
    <pre>
      //鼠标移入事件e解析
      e.clientX 返回鼠标相对于浏览器可视区域窗口的X坐标
      e.clientY 返回鼠标相对于浏览器可视区域窗口的Y坐标
      e.pageX 返回鼠标相对于文档页面的X坐标
      e.pageY 返回鼠标相对于文档页面的Y坐标
      e.screenX 返回鼠标相对于电脑屏幕的X坐标
      e.screenY 返回鼠标相对于电脑屏幕的Y坐标
    </pre>
    <h3>跟随鼠标移动案例</h3>
    <img src="../assets/jlt.png" class="img" />
    <pre>
      var img = document.querySelector(".img");
      document.onmousemove = function (e) {
        console.log(e.clientX, e.clientY);
        img.style.top = e.clientY - 25 + "px";
        img.style.left = e.clientX - 25 + "px";
      };
      <button @click="tianshi">点我绑定</button></pre>
    <h3>常用键盘事件</h3>
    <pre>
        onkeyup  键盘松开触发     //不识别大小写
        onkeydown 键盘按下时触发  //不识别大小写
        onkeypress 键盘按下时触发 //不识别功能键 识别大小写
        
        //event事件中
        keycode返回按键的ASCII码值  不区分按键的大小写
        </pre
    >
    <h3>案例-按下s键聚焦输入框</h3>
    <input type="text" id="shuru" />
    <pre>
      var input = document.querySelector("#shuru");
      document.onkeyup = function (e) {
        if (e.keyCode == 83) {
          input.focus();
        }
      };</pre
    >
    <h3>案例-放大输入表单</h3>
    <br />
    <div class="fangda">
      <input type="text" />
      <p></p>
    </div>
    <pre>
      var input = document.querySelector(".fangda").querySelector("input");
      var p = input.nextElementSibling;
      input.onkeyup = function () {
        if (input.value != "") {
          p.style.display = "block";
          p.innerText = input.value;
        } else {
          p.style.display = "none";
        }
      };
      input.onblur = function (e) {
        p.style.display = "none";
      };</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    tianshi() {
      var img = document.querySelector(".img");
      document.onmousemove = function (e) {
        console.log(e.clientX, e.clientY);
        img.style.top = e.clientY - 25 + "px";
        img.style.left = e.clientX - 25 + "px";
      };
    },
    jujiao() {
      var input = document.querySelector("#shuru");
      document.onkeyup = function (e) {
        if (e.keyCode == 83) {
          input.focus();
        }
      };
    },
    fangda() {
      var input = document.querySelector(".fangda").querySelector("input");
      var p = input.nextElementSibling;
      input.onkeyup = function () {
        if (input.value != "") {
          p.style.display = "block";
          p.innerText = input.value;
        } else {
          p.style.display = "none";
        }
      };
      input.onblur = function (e) {
        p.style.display = "none";
      };
    },
  },
  mounted() {
    this.jujiao();
    this.fangda();
  },
};
</script>

<style lang="less" >
.img {
  position: fixed;
  top: 100;
  left: 0;
  background-color: pink;
  width: 50px;
  height: 50px;
}
</style>