<template>
  <div class="Location">
    <h3>Location</h3>
    <li>Location 用来获取或设置浏览器的URL地址 并且可以解析URL</li>
    <img src="../assets/url.png" />
    <pre>
    console.log(location.href); //整个url
    console.log(location.host); //主机(域名)
    console.log(location.port); //端口号
    console.log(location.pathname); //路径
    console.log(location.search); //参数 比如?name="张三"&age=18
    console.log(location.hash); //字段 #后面的内容
    </pre>
    <h3>跳转页面</h3>
    <pre>
      var btn = document.querySelector(".gobd");
      btn.onclick = function () {
        location.href = "http://www.baidu.com";
      };
      <button class="gobd">去百度</button></pre>
    <h3>案例--页面不存在 5秒后返回</h3>
    <p class="p"></p>
    <pre>
      var p = document.querySelector(".p");
      var btn = document.querySelector(".fanhui");
      btn.onclick = function () {
        var time = 5;
        function dsq() {
          if (time == 0) {
            clearInterval(timer);
            location.href = "http://www.baidu.com";
          } else {
            p.innerText = time;
            time--;
          }
        }
        dsq();
        var timer = setInterval(dsq, 1000);
      };
      <button class="fanhui">点我试试</button></pre>
    <h3>案例--获取页面传递信息</h3>
    <pre>
        //页面一
        <img src="../assets/login.png" >
        //页面二
        <img src="../assets/index.png">
        //结果
        <img src="../assets/jg.png" >
      </pre>
    <h3>location常用方法</h3>
    <ul>
      <li>assign() 跳转页面</li>
      <li>replace() 替换当前页面</li>
      <li>reload() 刷新页面 如果参数true就强制刷新</li>
    </ul>
    <pre>
        location.assign("http://www.baidu.com")
        <button @click="assign">assign</button>
        location.replace("http://www.baidu.com");
        <button @click="replace">replace</button>
        location.reload(true);
        <button @click="reload">replace</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    gobd() {
      var btn = document.querySelector(".gobd");
      btn.onclick = function () {
        location.href = "http://www.baidu.com";
      };
    },
    fanhui() {
      var p = document.querySelector(".p");
      var btn = document.querySelector(".fanhui");
      btn.onclick = function () {
        var time = 5;
        function dsq() {
          if (time == 0) {
            clearInterval(timer);
            location.href = "http://www.baidu.com";
          } else {
            p.innerText = time;
            time--;
          }
        }
        dsq();
        var timer = setInterval(dsq, 1000);
      };
    },
    assign() {
      location.assign("http://www.baidu.com");
    },
    replace() {
      location.replace("http://www.baidu.com");
    },
    reload() {
      location.reload(true);
    },
  },
  computed: {},
  mounted() {
    this.gobd();
    this.fanhui();
    console.log(location.href); //整个url
    console.log(location.host); //主机(域名)
    console.log(location.port); //端口号
    console.log(location.pathname); //路径
    console.log(location.search); //参数 比如?name="张三"&age=18
    console.log(location.hash); //字段 #后面的内容
  },
};
</script>

