<template>
  <div class="setstyle">
    <h3>分时问候</h3>
    <li class="showtime"></li>
    <pre> 
      var a = document.querySelector(".showtime");
      var time = new Date().getHours();
      if (time < 12) {
        a.innerHTML = "上午好";
      } else if (time > 12 && time < 14) {
        a.innerHTML = "中午好";
      } else if (time > 14) {
        a.innerHTML = "下午好";
      } else if (tiem > 19) {
        a.innerHTML = "晚上好";
      }
      <button @click="shijian">点我试试</button></pre>
    <h3>输入密码</h3>
    <div class="mima">
      <input class="ipt" type="password" value="123" /><img
        class="img"
        src="../assets/yincang.png"
      />
    </div>
    <pre> 
      var flg = true;
      if (flg) {
        document.querySelector(".ipt").type = "text"; //下面这个路径正常引入就行，不用我这样写
        document.querySelector(".img").src = require("../assets/xianshi.png");
      } else {
        document.querySelector(".ipt").type = "password";
        document.querySelector(".img").src = require("../assets/yincang.png");
      }
      flg = !flg;
      <button @click="xsyc">点我试试</button>
    </pre>
    <h3>隐藏广告</h3>
    <div class="guangg">广告<span @click="cha">x</span></div>
    <pre>
      var a = document.querySelector(".guangg");
      a.style.display = "none";</pre
    >
    <h3>精灵图</h3>
    <div class="jlt">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <pre>
      var a = document.querySelector(".jlt");
      var list = a.querySelectorAll("li");
      for (var i = 0; i < list.length; i++) {
        var index = i * 44;
        list[i].style.backgroundPosition = "0 -" + index + "px";
      }</pre
    >
    <h3>文本框默认文字显示隐藏</h3>
    <input type="text" @focus="huoqu" @blur="shiqu" value="手机" id="wbk" />
    <pre>
 
      var a = document.querySelector("#wbk");
      a.onfocus = function () {
        if (a.value == "手机") {
          a.value = "";
        }
      };
      a.onblur = function () {
        if (a.value == "") {
          a.value = "手机";
        }
      };</pre
    >
    <h3>密码框验证</h3>
    <input type="text" @blur="yanz" id="yzmm" />
    <pre>
 
      var a = document.querySelector("#yzmm");
      if (a.value.length < 4 || a.value.length > 8) {
        alert("只能4-8个字符");
      }</pre
    >
    <h3>排他思想</h3>
    <button id="btn">按钮1</button>
    <button id="btn">按钮2</button>
    <button id="btn">按钮3</button>
    <pre>
      var a = document.querySelectorAll("#btn");
      for (var i = 0; i < a.length; i++) {
      a[i].onclick = function () {
        for (var j = 0; j < a.length; j++) {
          a[j].style.backgroundColor = "";
        }
        this.style.backgroundColor = "pink";
      };
      }</pre
    >
    <h3>点击图片切换背景</h3>
    <div class="baidu">
      <img src="../assets/xianshi.png" />
      <img src="../assets/yincang.png" />
      <span class="tup"></span>
    </div>
    <pre>
      var a = document.querySelector(".tup");
      var b = document.querySelector(".baidu").querySelectorAll("img");
      for (var i = 0; i < b.length; i++) {
        b[i].onclick = function () {
          a.style.background = "url(" + this.src + ")";
        };
      }
    </pre>
    <h3>移入行变色</h3>
    <ul class="bianse">
      <li>第一行</li>
      <li>第二行</li>
      <li>第三行</li>
    </ul>
    <pre>
      var a = document.querySelector(".bianse").querySelectorAll("li");
      for (var i = 0; i < a.length; i++) {
        a[i].onmouseover = function () {
          this.style.backgroundColor = "pink";
        };
        a[i].onmouseout = function () {
          this.style.backgroundColor = "";
        };
      }</pre
    >
    <h3>全选/反选</h3>
    <input type="checkbox" id="zhu" />全选<br />
    <input type="checkbox" id="fu" />
    <input type="checkbox" id="fu" />选中
    <pre>
      var a = document.querySelector("#zhu");
      var b = document.querySelectorAll("#fu");
      //全选
      a.onclick = function () {
        for (var i = 0; i < b.length; i++) {
          b[i].checked = a.checked;
        }
      };

      for (var i = 0; i < b.length; i++) {
        b[i].onclick = function () {
          var flag = true;
          for (var j = 0; j < b.length; j++) {
            if (!b[j].checked) {
              flag = false;
              break; //提高效率
            }
          }
          a.checked = flag;
        };
      }</pre
    >
  </div>
</template>

<script>
import "../assets/icon/iconfont.css";
export default {
  data() {
    return {
      flg: true,
    };
  },
  methods: {
    shijian() {
      var a = document.querySelector(".showtime");
      var time = new Date().getHours();
      if (time < 12) {
        a.innerHTML = "上午好";
      } else if (time > 12 && time < 14) {
        a.innerHTML = "中午好";
      } else if (time > 14) {
        a.innerHTML = "下午好";
      } else if (tiem > 19) {
        a.innerHTML = "晚上好";
      }
    },
    xsyc() {
      if (this.flg) {
        document.querySelector(".ipt").type = "text";
        document.querySelector(".img").src = require("../assets/xianshi.png");
      } else {
        document.querySelector(".ipt").type = "password";
        document.querySelector(".img").src = require("../assets/yincang.png");
      }
      this.flg = !this.flg;
    },
    cha() {
      var a = document.querySelector(".guangg");
      a.style.display = "none";
    },
    jlt() {
      var a = document.querySelector(".jlt");
      var list = a.querySelectorAll("li");
      for (var i = 0; i < list.length; i++) {
        var index = i * 44;
        list[i].style.backgroundPosition = "0 -" + index + "px";
      }
    },
    wenbenk() {
      var a = document.querySelector("#wbk");
      a.onfocus = function () {
        if (a.value == "手机") {
          a.value = "";
        }
      };
      a.onblur = function () {
        if (a.value == "") {
          a.value = "手机";
        }
      };
    },
    huoqu() {
      var a = document.querySelector("#wbk");
      if (a.value == "手机") {
        a.value = "";
      }
    },
    shiqu() {
      var a = document.querySelector("#wbk");
      if (a.value == "") {
        a.value = "手机";
      }
    },
    yanz() {
      var a = document.querySelector("#yzmm");
      if (a.value.length < 4 || a.value.length > 8) {
        alert("只能4-8个字符");
      }
    },
    bg() {
      var a = document.querySelector(".tup");
      var b = document.querySelector(".baidu").querySelectorAll("img");
      for (var i = 0; i < b.length; i++) {
        b[i].onclick = function () {
          a.style.background = "url(" + this.src + ")";
        };
      }
    },
    bianse() {
      var a = document.querySelector(".bianse").querySelectorAll("li");
      for (var i = 0; i < a.length; i++) {
        a[i].onmouseover = function () {
          this.style.backgroundColor = "pink";
        };
        a[i].onmouseout = function () {
          this.style.backgroundColor = "";
        };
      }
    },
    quanxuan() {
      var a = document.querySelector("#zhu");
      var b = document.querySelectorAll("#fu");
      //全选
      a.onclick = function () {
        for (var i = 0; i < b.length; i++) {
          b[i].checked = a.checked;
        }
      };

      for (var i = 0; i < b.length; i++) {
        b[i].onclick = function () {
          var flag = true;
          for (var j = 0; j < b.length; j++) {
            if (!b[j].checked) {
              flag = false;
              break; //提高效率
            }
          }
          a.checked = flag;
        };
      }
    },
  },
  mounted() {
    this.jlt();
    //this.wenbenk();
    this.bg();
    this.bianse();
    this.quanxuan();

    var a = document.querySelectorAll("#btn");
    for (var i = 0; i < a.length; i++) {
      a[i].onclick = function () {
        for (var j = 0; j < a.length; j++) {
          a[j].style.backgroundColor = "";
        }
        this.style.backgroundColor = "pink";
      };
    }
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.mima {
  width: 200px;
  border-bottom: 1px solid #ccc;
  position: relative;
  input {
    outline: none;
    border: none;
    height: 20px;
    width: 100%;
  }
  img {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 2px;
    top: 2px;
  }
}
.guangg {
  background-color: pink;
  position: relative;
  width: 100px;
  height: 100px;
  text-align: center;
  line-height: 100px;
  font-size: 20px;
  font-weight: 700;
  span {
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 10px;
    line-height: 20px;
  }
}
.jlt {
  ul {
    overflow: hidden;
    li {
      width: 24px;
      height: 24px;
      float: left;
      margin-left: 10px;
      list-style: none;
      background-image: url(../assets/jlt.png);
    }
  }
}
.tup {
  width: 200px;
  height: 200px;
  background-color: pink;
  display: inline-block;
}
</style>