<template>
  <div class="Timeout">
    <h3>定时器</h3>
    <li>setTimeout() 设置时间 延时触发</li>
    <pre>
      //setTimeout()内放入函数和时间(毫秒)
      setTimeout(function () {
        console.log("时间到了1");
      }, 2000);

      //也可以把函数写在外面调用
      function timeout() {
        console.log("时间到了2");
      }
      setTimeout(timeout, 2000);

      //这样也行
      var timeout2 = function () {
        console.log("时间到了3");
      };
      setTimeout(timeout2, 2000);

      //给定时器起个名字方便管理
      var timer1 = setTimeout(timeout2, 2000);
     <button @click="timeout">点我试试</button></pre>
    <h3>案例--三秒自动关闭广告</h3>
    <div class="guangg">广告</div>
    <pre>
      var div = document.querySelector(".guangg");
      var btn = document.querySelector(".gb");
      btn.onclick = function () {
        setTimeout(function () {
          div.style.display = "none";
        }, 3000);
      };
       <button class="gb">倒计时关闭</button></pre>
    <h3>清除定时器</h3>
    <li>setTimeout</li>
    <pre>
      function timeout() {
        console.log("时间到了");
      }
      var timer1 = setTimeout(timeout, 200000);
      var btn = document.querySelector(".stop");
      btn.onclick = function () {
        clearTimeout(timer1);
      };
      <button class="stop">停止</button></pre>
    <h3>定时器</h3>
    <li>setInterval(函数,间隔毫秒数) 这个定时器会每隔几秒执行一次</li>
    <pre>
      var timer = setInterval(function () {
        console.log("执行了");
      }, 2000);
      <button @click="dingshiqi()">启动</button></pre>
    <h3>清除定时器</h3>
    <li>setInterval</li>
    <pre>
      function timeout() {
        console.log("触发了");
      }
      var timer1 = setInterval(timeout, 2000);
      var btn = document.querySelector(".stop2");
      btn.onclick = function () {
        clearInterval(timer1);
      };
      <button class="stop2">停止</button></pre>
    <h3>案例--倒计时</h3>
    <p class="daojishi"></p>
    <pre>
      //页面刷新有空白  页面加载先调用一次函数再开启定时器就行了
      var p = document.querySelector(".daojishi");
      var time = 5;
      var timer = setInterval(function () {
        if (time == -1) {
          clearInterval(timer);
          p.style.display = "none";
        } else {
          p.innerText = time;
          time--;
        }
      }, 1000);
      <button @click="daojishi">开始倒计时</button></pre>
    <h3>案例-60秒发送短信</h3>
    <div class="duanxin"><input type="text" /><button>发送</button></div>
    <pre>
      var div = document.querySelector(".duanxin");
      var btn = div.children[1];
      var time = 5;
      btn.onclick = function () {
        btn.disabled = true;
        timeout();
        var timer1 = setInterval(timeout, 1000);
        function timeout() {
          if (time != 0) {
            btn.innerText = time;
            time--;
          } else {
            btn.innerText = "发送";
            btn.disabled = false;
            clearInterval(timer1);
            time = 5;
          }
        }
      };
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    timeout() {
      //setTimeout()内放入函数和时间(毫秒)
      setTimeout(function () {
        console.log("时间到了1");
      }, 2000);

      //也可以把函数写在外面调用
      function timeout() {
        console.log("时间到了2");
      }
      setTimeout(timeout, 2000);

      //这样也行
      var timeout2 = function () {
        console.log("时间到了3");
      };
      setTimeout(timeout2, 2000);

      //给定时器起个名字方便管理
      var timer1 = setTimeout(timeout2, 2000);
    },
    guangg() {
      var div = document.querySelector(".guangg");
      var btn = document.querySelector(".gb");
      btn.onclick = function () {
        setTimeout(function () {
          div.style.display = "none";
        }, 3000);
      };
    },
    tingzhi() {
      function timeout() {
        console.log("时间到了");
      }
      var timer1 = setTimeout(timeout, 200000);
      var btn = document.querySelector(".stop");
      btn.onclick = function () {
        clearTimeout(timer1);
      };
    },
    dingshiqi() {
      var timer = setInterval(function () {
        console.log("执行了");
      }, 2000);
    },
    daojishi() {
      var p = document.querySelector(".daojishi");
      var time = 5;
      var timer = setInterval(function () {
        if (time == -1) {
          clearInterval(timer);
          p.style.display = "none";
        } else {
          p.innerText = time;
          time--;
        }
      }, 1000);
    },
    dsq() {
      function timeout() {
        console.log("触发了");
      }
      var timer1 = setInterval(timeout, 2000);
      var btn = document.querySelector(".stop2");
      btn.onclick = function () {
        clearInterval(timer1);
      };
    },
    duanxin() {
      var div = document.querySelector(".duanxin");
      var btn = div.children[1];
      var time = 5;
      btn.onclick = function () {
        btn.disabled = true;
        timeout();
        var timer1 = setInterval(timeout, 1000);
        function timeout() {
          if (time != 0) {
            btn.innerText = time;
            time--;
          } else {
            btn.innerText = "发送";
            btn.disabled = false;
            clearInterval(timer1);
            time = 5;
          }
        }
      };
    },
  },
  mounted() {
    this.guangg();
    this.tingzhi();
    this.dsq();
    this.duanxin();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.guangg {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
}
.duanxin {
  width: 200px;
  overflow: hidden;
  input {
    width: 140px;
    float: left;
  }
  button {
    width: 50px;
    float: right;
  }
}
</style>