<template>
  <div class="Intdemo">
    <h3>获取自定义属性</h3>
    <pre>
      <input type="text" index="1" id="zdy" />
      var a = document.querySelector("#zdy");
      var b = a.getAttribute("index");
      console.log(b);
      <button @click="zdy">点我试试</button></pre>
    <h3>修改/设置自定义属性</h3>
    <pre>
        <input type="text" id="setzdy" index="1">
         var a = document.querySelector("#setzdy");
         a.setAttribute("index", "2");
         console.log(a.getAttribute("index"));
        <button @click="setzdy">点我试试</button></pre>
    <h3>移除自定义属性</h3>
    <pre>
      <input type="text" index="1" id="del">
      var a = document.querySelector("#del");
      a.removeAttribute("index");
      <button @click="del">点我试试</button></pre>
    <h3>自定义属性规范</h3>
    <li>自定义属性一定使用data-开头</li>
    <h3>新增获取data开头自定义属性方式</h3>
    <pre>
        <input type="text" id="xinadd" data-index = "1">
      var a = document.querySelector("#xinadd");
      //dataset是所有data开头的自定义属性集合
      console.log(a.dataset.index);
      //如果自定义属性是data-list-anni需要驼峰命名法
      a.dataset.listAnni
        <button @click="xinadd">点我试试</button>
      </pre>
    <h3>tab栏切换案例</h3>
    <div class="tab">
      <ul>
        <li class="tabsty">第一个</li>
        <li>第二个</li>
        <li>第三个</li>
      </ul>
      <br />
      <div class="block">一</div>
      <div>二</div>
      <div>三</div>
    </div>
    <pre>
      //获取元素
      var a = document.querySelector(".tab");
      var b = a.querySelectorAll("li");
      var c = a.querySelectorAll("div");
      for (var i = 0; i < b.length; i++) {
        //给tab设置自定义属性
        b[i].setAttribute("index", i);
        //绑定点击事件
        b[i].onclick = function () {
          //干掉其他的类
          for (var j = 0; j < b.length; j++) {
            b[j].className = "";
          }
          //留下自己
          this.className = "tabsty";
          //获取当前点击的index
          var index = this.getAttribute("index");
          //干掉其他的
          for (var i = 0; i < b.length; i++) {
            c[i].style.display = "none";
          }
          c[index].style.display = "block";
        };
      }</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    zdy() {
      var a = document.querySelector("#zdy");
      var b = a.getAttribute("index");
      console.log(b);
    },
    setzdy() {
      var a = document.querySelector("#setzdy");
      a.setAttribute("index", "2");
      console.log(a.getAttribute("index"));
    },
    del() {
      var a = document.querySelector("#del");
      a.removeAttribute("index");
    },
    xinadd() {
      var a = document.querySelector("#xinadd");
      console.log(a.dataset.index);
    },
    tab() {
      //获取元素
      var a = document.querySelector(".tab");
      var b = a.querySelectorAll("li");
      var c = a.querySelectorAll("div");
      for (var i = 0; i < b.length; i++) {
        //给tab设置自定义属性
        b[i].setAttribute("index", i);
        //绑定点击事件
        b[i].onclick = function () {
          //干掉其他的类
          for (var j = 0; j < b.length; j++) {
            b[j].className = "";
          }
          //留下自己
          this.className = "tabsty";
          //获取当前点击的index
          var index = this.getAttribute("index");
          //干掉其他的
          for (var i = 0; i < b.length; i++) {
            c[i].style.display = "none";
          }
          c[index].style.display = "block";
        };
      }
    },
  },
  mounted() {
    this.tab();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.tab {
  padding-left: 30px;
  overflow: hidden;
  li {
    list-style: none;
    float: left;
    margin-right: 20px;
    cursor: pointer;
  }
  .block {
    display: block;
  }
  div {
    display: none;
  }
}
.tabsty {
  background-color: red;
  color: #fff;
}
</style>