<template>
  <div class="Local">
    <h3>本地存储</h3>
    <li>数据存储在用户浏览器中</li>
    <li>以键值对方式存储</li>
    <li>只能存储字符串</li>
    <li>JSON.stringify(xxx) 转换为字符串即可存储</li>
    <li>JSON.parse(xxx) 取出转换为对象</li>
    <pre>
      //sessionStorage
      //关闭浏览器窗口销毁数据
      //同一窗口(页面可以数据共享)

      //存储数据
      sessionStorage.setItem(key,value)
      //获取数据
      sessionStorage.getItem(key)
      //删除数据
      sessionStorage.removeItem(key)
      //删除所有数据
      sessionStorage.clear()

      //localStorage
      //浏览器永久有效 除非手动删除
      //多个窗口(页面)可以共享数据

      //存储数据
      localStorage.setItem(key,value)
      //获取数据
      localStorage.getItem(key)
      //删除数据
      localStorage.removeItem(key)
      //删除所有数据
      localStorage.clear()
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

