<template>
  <div class="Domdemo">
    <h3>注册(绑定)事件</h3>
    <pre>
      var a = document.querySelector(".ck");
      //传统方式
      a.onclick = function () {
        console.log("传统方式");
      }; //缺点 多个事件会覆盖

      //监听方式
      a.addEventListener("click", function () {
        console.log("监听方式");
      });
      <button class="ck" @click="bdsj">点我试试</button></pre>
    <h3>删除(解绑)事件</h3>
    <pre>
      var a = document.querySelector(".jiebang");
      //传统方式
      a.onclick = function () {
        console.log("解绑了");
        a.onclick = null;
      };
      //监听方式
      function fu() {
        console.log("解绑了");
        a.removeEventListener("click", fu);
      }
      a.addEventListener("click", fu);
        <button class="jiebang">点我试试</button>
      </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    bdsj() {
      var a = document.querySelector(".ck");
      //传统方式
      a.onclick = function () {
        console.log("传统方式");
      }; //缺点 多个事件会覆盖

      //监听方式
      a.addEventListener("click", function () {
        console.log("监听方式");
      });
    },
    jiebang() {
      var a = document.querySelector(".jiebang");
      //传统方式
      a.onclick = function () {
        console.log("解绑了");
        a.onclick = null;
      };
      //监听方式
      function fu() {
        console.log("解绑了");
        a.removeEventListener("click", fu);
      }
      a.addEventListener("click", fu);
    },
  },
  mounted() {
    this.jiebang();
  },
  computed: {},
};
</script>

