<template>
  <div class="Anli2">
    <h3>案例一 带有动画的返回顶部</h3>
    <li>window.scroll(0,0);</li>
    <li>封装的动画函数修改</li>
    <li>obj.offsetLeft 修改成window.pageYOffset</li>
    <pre>
      //修改封装动画函数
    donghua(obj, target, fun) {
    clearInterval(obj.timer);
    obj.timer = setInterval(function () {
      var step = (target - window.pageYOffset) / 10;
      step = step > 0 ? Math.ceil(step) : Math.floor(step);
      if (window.pageYOffset == target) {
        clearInterval(obj.timer);
        if (fun) {
          fun();
        }
      }
      //obj.style.left = obj.offsetLeft + step + "px";
      window.scroll(0,window.pageYOffset+step);
    }, 15);
  },
    </pre>
    <pre>
      //点击事件中直接调用
      donghua(window,0);
    </pre>
    <h3>案例二 tab动画切换</h3>
    <pre>
   //封装的动画函数
    donghua(obj, target, fun) {
    clearInterval(obj.timer);
    obj.timer = setInterval(function () {
      var step = (target - obj.offsetLeft) / 10;
      step = step > 0 ? Math.ceil(step) : Math.floor(step);
      if (obj.offsetLeft == target) {
        clearInterval(obj.timer);
        if (fun) {
          fun();
        }
      }
      obj.style.left = obj.offsetLeft + step + "px";
    }, 15);
  },
    </pre>
    <pre>
    //current 记录初始位置
    var current = 0;
    //循环给tab中的li绑定事件
    for (var i = 0; i < list.length; i++) {
      //绑定鼠标进入事件
      list[i].onmouseenter = function () {
        donghua(cloud, this.offsetLeft);
      };
      //绑定鼠标移出事件
      list[i].onmouseleave = function () {
        donghua(cloud, current);
      };
      //绑定鼠标点击事件
      list[i].onclick = function () {
        current = this.offsetLeft;
      };
    }
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

