<template>
  <el-container class="home_container">
    <!-- 头部区域 -->
    <el-header class="header">
      <div class="left_header">
        <img :src="qita.img" />
        <span>{{ qita.title }}</span>
      </div>
      <!--  <el-button type="info" @click="logout">退出</el-button> -->
    </el-header>
    <!-- 页面主题区域 -->
    <el-container class="main_con">
      <!-- 侧边栏 -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-button">|||</div>
        <!--  @click="toggleCollapse" -->
        <!-- 侧边栏菜单区 -->
        <el-menu
          unique-opened
          background-color="#313743"
          text-color="#fff"
          active-text-color="#409eff"
          :collapse="isCollapse"
          :collapse-transition="false"
          :router="true"
          :default-active="$route.path"
        >
          <!-- 一级菜单 -->
          <el-submenu
            :index="'/' + item.path"
            v-for="(item, index) in menuList"
            :key="item + index"
          >
            <!-- 一级菜单的模板区 -->
            <template slot="title">
              <!-- 图标 -->

              <!-- 文本 -->
              <span>{{ item.authName }}</span>
            </template>
            <el-menu-item
              :index="'/' + item2.path"
              v-for="(item2, index) in item.children"
              :key="item2 + index"
            >
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <!-- 文本 -->
                <span>{{ item2.authName }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 主题内容 -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import menuList from "./routers";
export default {
  name: "Home",
  data() {
    return {
      menuList: "",
      qita: "",
      isCollapse: false,
    };
  },
  mounted() {
    this.menuList = menuList.List;
    this.qita = menuList;
  },
  methods: {
    logout() {
      window.sessionStorage.clear();
      alert("清除完毕");
    },

    //点击按钮，切换菜单的展开和折叠
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
  },
};
</script>

<style lang="less" scoped>
.el-main .home_container {
  height: 100%;
}

.el-header {
  background-color: #363d40;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .left_header {
    display: flex;
    align-items: center;
    img {
      width: 60px;
      border-radius: 50%;
    }
    span {
      color: white;
      font-size: 18px;
      margin-left: 15px;
    }
  }
}

.el-aside {
  background-color: #313743;
  .iconfont {
    margin-right: 10px;
  }
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}
.main_con {
  height: calc(100vh - 60px);
}

.toggle-button {
  color: white;
  font-size: 12px;
  padding: 3px 0;
  letter-spacing: 0.2em;
  background-color: #495065;
  text-align: center;
  cursor: pointer;
}
.toggle-button :hover {
  color: #409eff;
}
</style>
