<template>
  <div class="Qitabom">
    <h3>navigator对象</h3>
    <li>包含有关浏览器的信息，常用userAgent</li>
    <pre>
      console.log(navigator);
      console.log(navigator.userAgent);
      <button @click="ceshi">点我试试</button></pre>
    <h3>history对象</h3>
    <li>back() 后退</li>
    <li>forward() 前进</li>
    <li>go(参数) 自定义前进后退</li>
    <li>与浏览器历史记录交互(包含用户输入地址访问过的记录)</li>
    <pre>
         history.back();
        <button @click="back">back</button>
         history.forward();
        <button @click="forward">forward</button>
         //前进是+ 后退是- 进退几步就是几
         history.go(-1);
        <button @click="go">go</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    ceshi() {
      console.log(navigator);
      console.log(navigator.userAgent);
    },
    back() {
      history.back();
    },
    forward() {
      history.forward();
    },
    go() {
      //前进是+ 后退是- 进退几步就是几
      history.go(-1);
    },
  },
  computed: {},
  mounted() {},
};
</script>

