<template>
  <div class="Shijianliu">
    <h3>事件流</h3>
    <p>事件的触发是按照事件流的</p>
    <p>比如DOM有html，head，和div</p>
    <p>div触发事件会从上到下捕获事件</p>
    <p>捕获到div后触发还会往上冒泡</p>
    <p>冒泡依次触发上面所有事件</p>
    <ul>
      <li>捕获阶段</li>
      <li>当前目标阶段</li>
      <li>冒泡阶段</li>
    </ul>
    <h3>事件流</h3>
    <li>捕获阶段</li>
    <div class="wai">
      <div class="nei">点我</div>
    </div>
    <pre>
      //事件流先捕获，从外到内触发捕获层
      var wai = document.querySelector(".wai");
      var nei = document.querySelector(".nei");
      //true表示触发在捕获层
      wai.addEventListener(
        "click",
        function () {
          alert("外面的盒子触发了");
        },
        true
      );
      nei.addEventListener(
        "click",
        function () {
          alert("里面的盒子触发了");
        },
        true
      );</pre
    >
    <h3>事件流</h3>
    <li>冒泡阶段</li>
    <li>onclick属于冒泡流</li>
    <div class="wai2">
      <div class="nei2">点我</div>
    </div>
    <pre>
      //事件流冒泡，由内而外触发
      var wai = document.querySelector(".wai2");
      var nei = document.querySelector(".nei2");
      //false表示触发在冒泡层
      wai.addEventListener(
        "click",
        function () {
          alert("外面的盒子触发了");
        },
        false //默认就是false
      );
      nei.addEventListener(
        "click",
        function () {
          alert("里面的盒子触发了");
        },
        false
      );
    </pre>
    <h3>事件对象</h3>
    <pre>
      var a = document.querySelector(".btn");
      //传统方式
      //event是默认传递的参数，是一个事件对象集合
      a.onclick = function (event) {
        console.log(event);
      };
      //监听方式
      a.addEventListener("click", function (event) {
        console.log(event);
      });
      <button class="btn">点我试试</button>
    </pre>
    <h3>常用事件对象属性</h3>
    <ul>
      <li>e.target 触发事件的对象</li>
      <li>e.type 事件的类型 比如click mouseover</li>
      <li>e.preventDefault() 阻止默认行为 比如跳转</li>
      <li>e.stopPropagation() 阻止冒泡</li>
    </ul>
    <pre>
      var btn = document.querySelector("#ck");
      btn.onclick = function (e) {
        console.log(e.target); //触发对象
        console.log(e.type); //触发类型
        e.preventDefault(); //阻止默认行为
        e.stopPropagation(); //阻止冒泡
      };
      <button id="ck">点我试试</button></pre>
    <h3>事件委派</h3>
    <ul class="weipai">
      <li>第一行</li>
      <li>第二行</li>
      <li>第三行</li>
    </ul>
    <pre>
      //只绑定了一个点击事件
      var ul = document.querySelector(".weipai");
      ul.onclick = function (e) {
        console.log(e.target.innerText);
      };</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    shijianliu() {
      //事件流先捕获，从外到内触发捕获层
      var wai = document.querySelector(".wai");
      var nei = document.querySelector(".nei");
      //true表示触发在捕获层
      wai.addEventListener(
        "click",
        function () {
          alert("外面的盒子触发了");
        },
        true
      );
      nei.addEventListener(
        "click",
        function () {
          alert("里面的盒子触发了");
        },
        true
      );
    },
    shijianliu2() {
      //事件流冒泡，由内而外触发
      var wai = document.querySelector(".wai2");
      var nei = document.querySelector(".nei2");
      //false表示触发在冒泡层
      wai.addEventListener(
        "click",
        function () {
          alert("外面的盒子触发了");
        },
        false //默认就是false
      );
      nei.addEventListener(
        "click",
        function () {
          alert("里面的盒子触发了");
        },
        false
      );
    },
    shijianduixiang() {
      var a = document.querySelector(".btn");
      //传统方式
      //event是默认传递的参数，是一个事件对象集合
      a.onclick = function (event) {
        console.log(event);
      };
      //监听方式
      a.addEventListener("click", function (event) {
        console.log(event);
      });
    },
    shijian() {
      var btn = document.querySelector("#ck");
      btn.onclick = function (e) {
        console.log(e.target); //触发对象
        console.log(e.type); //触发类型
        e.preventDefault(); //阻止默认行为
        e.stopPropagation(); //阻止冒泡
      };
    },
    weipai() {
      var ul = document.querySelector(".weipai");
      ul.onclick = function (e) {
        console.log(e.target.innerText);
      };
    },
    
  },
  mounted() {
    this.shijianliu();
    this.shijianliu2();
    this.shijianduixiang();
    this.shijian();
    this.weipai();
    
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.wai {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  align-items: center;
  .nei {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 50px;
    height: 50px;
    background-color: pink;
  }
}
.wai2 {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  align-items: center;
  .nei2 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 50px;
    height: 50px;
    background-color: pink;
  }
}
</style>