<template>
  <div class="Bomdemo">
    <h3>BOM概述</h3>
    <ol>
      <li>BOM是整个浏览器 包含DOM(浏览器窗口文档)</li>
      <li>BOM是把window当成顶级对象 而DOM是把document当成顶级对象</li>
      <li>BOM中主要学习浏览器窗口交互的一些对象</li>
    </ol>
    <img src="../assets/BOM.png" />
    <h3>window对象常见事件</h3>
    <li>onload 页面加载完毕事件</li>
    <li>注意!! 传统事件只能写一个 监听事件可以是多个</li>
    <pre>
    //传统方式
    window.onload = function () {};
    //监听器方式
    window.addEventListener("load", function () {});</pre
    >
    <h3>window对象常见事件</h3>
    <li>DOMContentLoaded 页面文档加载完毕事件(不包含样式 图片 flash)</li>
    <pre>
    document.addEventListener("DOMContentLoaded", function () {});</pre
    >
    <h3>window对象常见事件</h3>
    <li>onresize 窗口大小调整事件</li>
    <li>window.innerWidth 获取当前屏幕宽度</li>
    <pre>
    //传统方式
    window.onresize = function () {};
    //监听方式
    window.addEventListener("resize", function () {});</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {},
  mounted() {
    //传统方式
    window.onresize = function () {};
    //监听方式
    window.addEventListener("resize", function () {});
  },
  computed: {},
};
</script>

