<template>
  <div class="Dffects">
    <h3>offset元素偏移量</h3>
    <li>获得元素距离带有定位父元素的位置</li>
    <li>获取自身的大小</li>
    <hr />
    <li>offsetParent 返回带有定位的父元素</li>
    <li>offsetTop 上方距离定位父元素的距离</li>
    <li>offsetLeft 左边距离定位父元素的距离</li>
    <li>offsetWidth 自身的宽度 包含边框和padding 返回数字不带单位</li>
    <li>offsetHeight 自身的高度 包含边框和padding 返回数字不带单位</li>
    <div class="pianyi"></div>
    <pre>
      var div = document.querySelector(".pianyi");
      console.log(div.offsetTop);
      console.log(div.offsetLeft);
      //包含border padding width 返回值不带单位
      console.log(div.offsetWidth);
      console.log(div.offsetHeight);
      //找到带有定位的父元素,没有就返回body
      console.log(div.offsetParent);
      <button @click="pianyi">点我试试</button></pre>
    <h3>offset和style的区别</h3>
    <ol>
      <li>style只能拿到行内样式 通常用来修改样式 (也是改在了行内)</li>
      <li>offset可以拿到元素的所有样式</li>
    </ol>
    <hr />
    <ol>
      <li>style获取到的元素带有单位 是字符串类型</li>
      <li>offset获取到的元素不带单位 是数字型</li>
    </ol>
    <hr />
    <ol>
      <li>style.width获取的 不包含padding和border</li>
      <li>offsetWidth获取的 包含padding和border</li>
    </ol>
    <hr />
    <ol>
      <li>style 可以读写 (适合修改元素)</li>
      <li>offset 只能读取 (适合获取元素)</li>
    </ol>
    <h3>案例--点击获取鼠标在盒子内的位置</h3>
    <div class="box"></div>
    <pre>
      //给document绑定点击事件
      document.onclick = function (e) {
        //获取点击位置与浏览器的距离
        var bodyLeft = e.pageX;
        console.log("鼠标距离浏览器距离", bodyLeft);
        //获取盒子和带有定位父元素的距离
        var box = document.querySelector(".box");
        var boxLeft = box.offsetLeft;
        console.log("盒子距离父定位盒子距离(body)" + boxLeft);
        //鼠标距离浏览器距离减去盒子距离body距离 =鼠标距离盒子距离
        console.log("鼠标距离盒子距离", bodyLeft - boxLeft);
      };
    <button @click="zuobiao">点我试试</button></pre>
    <h3>案例--拖拽模态框</h3>
    <pre>
      <button @click="tanchu">弹出模态框</button>
          var a = document.querySelector(".tanchu");
          //绑定按下事件
          a.addEventListener("mousedown", function (e) {
            //获取到鼠标在盒子内的距离
            var x = e.pageX - a.offsetLeft;
            var y = e.pageY - a.offsetTop;
            //定义鼠标移动方法 设置left 和top
            function fn(e) {
              a.style.left = e.pageX - x + "px";
              a.style.top = e.pageY - y + "px";
            }
            //绑定鼠标移动事件 频繁改变盒子top和left
            document.addEventListener("mousemove", fn);
            //绑定松开事件,解除document的鼠标移动事件
            document.addEventListener("mouseup", function () {
              document.removeEventListener("mousemove", fn);
            });
          })
    </pre>
    <div class="quanp" v-if="flg">
      <div class="tanchu">
        弹出层
        <span @click="flg = !flg">关闭</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flg: false,
    };
  },
  methods: {
    pianyi() {
      var div = document.querySelector(".pianyi");
      console.log(div.offsetTop);
      console.log(div.offsetLeft);
      console.log(div.offsetWidth);
      console.log(div.offsetHeight);
      console.log(div.offsetParent);
    },
    zuobiao() {
      //给document绑定点击事件
      document.onclick = function (e) {
        //获取点击位置与浏览器的距离
        var bodyLeft = e.pageX;
        console.log("鼠标距离浏览器距离", bodyLeft);
        //获取盒子和带有定位父元素的距离
        var box = document.querySelector(".box");
        var boxLeft = box.offsetLeft;
        console.log("盒子距离父定位盒子距离(body)" + boxLeft);
        //鼠标距离浏览器距离减去盒子距离body距离 =鼠标距离盒子距离
        console.log("鼠标距离盒子距离", bodyLeft - boxLeft);
      };
    },
    tanchu() {
      this.flg = !this.flg;
      setTimeout(() => {
        var a = document.querySelector(".tanchu");
        a.addEventListener("selectstart", function (e) {
          e.preventDefault();
          //绑定按下事件
          a.addEventListener("mousedown", function (e) {
            //获取到鼠标在盒子内的距离
            var x = e.pageX - a.offsetLeft;
            var y = e.pageY - a.offsetTop;
            //定义鼠标移动方法 设置left 和top
            function fn(e) {
              a.style.left = e.pageX - x + "px";
              a.style.top = e.pageY - y + "px";
            }
            //绑定鼠标移动事件 频繁改变盒子top和left
            document.addEventListener("mousemove", fn);
            //绑定松开事件,解除document的鼠标移动事件
            document.addEventListener("mouseup", function () {
              document.removeEventListener("mousemove", fn);
            });
          });
        });
      }, 0);
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style>
.pianyi {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
  margin: 50px;
}
.box {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
  margin: 50px;
}
.tanchu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 200px;
  line-height: 200px;
  background-color: pink;
  text-align: center;
  cursor: move;
}
.tanchu span {
  position: absolute;
  right: 0;
  top: 0;
  line-height: 30px;
  transform: translate(50%, -50%);
  background-color: rgb(255, 2, 2);
  border-radius: 50%;
  cursor: pointer;
}
.quanp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
</style>