<template>
  <div class="get">
    <h3 id="h3">通过id获取</h3>
    <pre>
       var a = document.getElementById("h3")
       console.log(a);
       <button @click="byid">点我试试</button></pre>
    <h3>通过标签名获取</h3>
    <pre>
      var a = document.getElementsByTagName("h3");
      console.log(a);
      <button @click="name">点我试试</button></pre>
    <h3 class="h3">通过class获取</h3>
    <pre>
      var a = document.getElementsByClassName("h3");
      console.log(a);
       <button @click="byclass">点我试试</button></pre>
    <h3>选择器获取</h3>
    <pre>
      //只能返回选择器的第一个对象
      var a = document.querySelector(".h3");
      console.log(a);
      <button @click="qslt">点我试试</button></pre>
    <h3>选择器获取 2</h3>
    <pre>
      //返回符合选择器的所有对象
       var a = document.querySelectorAll("h3");
      console.log(a);
      <button @click="qslt2">点我试试</button></pre>
    <h3>获取body</h3>
    <pre>
      var a = document.body;
      console.log(a);
        <button @click="body">点我试试</button></pre>
    <h3>获取html</h3>
    <pre>
      var a = document.documentElement;
      console.log(a);
        <button @click="html">点我试试</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    main() {},
    byid() {
      var a = document.getElementById("h3");
      console.log(a);
    },
    name() {
      var a = document.getElementsByTagName("h3");
      console.log(a);
    },
    byclass() {
      var a = document.getElementsByClassName("h3");
      console.log(a);
    },
    qslt() {
      var a = document.querySelector(".h3");
      console.log(a);
    },
    qslt2() {
      var a = document.querySelectorAll("h3");
      console.log(a);
    },
    body() {
      var a = document.body;
      console.log(a);
    },
    html() {
      var a = document.documentElement;
      console.log(a);
    },
  },
  mounted() {
    this.main();
  },
  computed: {},
};
</script>

