<template>
  <div class="set">
    <h3>点击事件</h3>
    <pre>
       var a = document.getElementById("clickId");
       a.onclick = function () {
       console.log("我被点击了");
      };
    <button id="clickId" @click="ck">点我试试</button></pre>
    <h3>事件类型</h3>
    <pre>
      <img src="../assets/on.png" ></pre>
    <h3>改变元素内容</h3>
    <li>innerText</li>
    <pre> 
      //innerText只能修改文字内容,不能解析标签(去除换行空格)
      var a = document.querySelector("#setpre");
      a.onclick = function () {
        a.innerText = "我被点击了";
      };
      <button id="setpre" @click="sethtml">点我试试</button></pre>
    <h3>改变元素内容</h3>
    <li>innerHtml</li>
    <pre> 
      //innerHTML能解析html标签(保留换行空格)
      var a = document.querySelector("#setpre");
      a.onclick = function () {
        a.innerHTML = "我被点击了";
      };
      <button id="setpre2" @click="sethtml2">点我试试</button></pre>
    <h3>常见元素属性操作</h3>
    <ul>
      <li>src</li>
      <li>href</li>
      <li>id</li>
      <li>alt</li>
      <li>title</li>
    </ul>
    <a id="a" target="_block" href="http://www.baidu.com">测试用</a>
    <img class="img" title="测试" src="../assets/on.png" alt="测试用" />
    <pre>
      var a = document.querySelector("#a");
      a.href = "http://www.4399.com";
      a.id = "b"
      var b = document.querySelector(".img");
      b.title = "改变了";
      b.src = "https://gitee.com/elfin-chen/bed/raw/master/img/20210612202700.jpg"
      b.alt = "改变了"
      <button @click="changjian">点我试试</button></pre>
    <h3>操作表单元素</h3>
    <li>value</li>
    <li>checked</li>
    <li>selected</li>
    <li>disabled</li>
    <li>type</li>
    <input type="password" id="ipt" />
    <pre>
      var a = document.querySelector("#ipt");
      a.value = "123";
      a.disabled = true;
      a.type = "text"
      <button @click="biaodan">点我试试</button></pre>
    <h3>修改样式</h3>
    <div class="style"></div>
    <pre>
      var a = document.querySelector(".style");
      //修改class 会覆盖原来的类名
      a.className = "stylegai";
      //js修改样式是行内样式，权重比较高
      //修改样式采用驼峰命名
      a.style.width = "200px";
      a.style.height = "200px";
      a.style.backgroundColor = "red";
      a.style.borderRadius = "20px";
      <button @click="setstyle">点我试试</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    ck() {
      var a = document.getElementById("clickId");
      a.onclick = function () {
        console.log("我被点击了");
      };
    },
    sethtml() {
      var a = document.querySelector("#setpre");
      a.innerText = "我被点击了";
    },
    sethtml2() {
      var a = document.querySelector("#setpre2");
      a.innerHTML = "我被点击了";
    },
    changjian() {
      var a = document.querySelector("#a");
      a.href = "http://www.4399.com";
      a.id = "b";
      var b = document.querySelector(".img");
      b.title = "改变了";
      b.src =
        "https://gitee.com/elfin-chen/bed/raw/master/img/20210612202700.jpg";
      b.alt = "改变了";
    },
    biaodan() {
      var a = document.querySelector("#ipt");
      a.value = "123";
      a.disabled = true;
      a.type = "text";
    },
    setstyle() {
      var a = document.querySelector(".style");
      //修改class 会覆盖原来的类名
      a.className = "stylegai";
      //js修改样式是行内样式，权重比较高
      //修改样式采用驼峰命名
      a.style.width = "200px";
      a.style.height = "200px";
      a.style.backgroundColor = "red";
      a.style.borderRadius = "20px";
    },
  },
};
</script>

<style lang="less" scoped>
.img {
  width: 100px;
  height: 100px;
}
.style {
  width: 100px;
  height: 100px;
  background-color: pink;
}
</style>