<template>
  <div class="Carousel">
    <h3>效果展示</h3>
    <div class="lunbo">
      <div class="content" @mouseenter="jinru" @mouseleave="chuqu">
        <div class="div1"></div>
        <div class="div2"></div>
        <div class="div3"></div>
      </div>
      <div class="left" @click="zuo">&lt;</div>
      <div class="right" @click="you">&gt;</div>
      <div>
        <ul class="list"></ul>
      </div>
    </div>
    <br /><br /><br />
    <h3>搭建页面结构</h3>
    <li>html</li>
    <pre v-text="html"></pre>
    <li>css</li>
    <pre v-text="css"></pre>
    <li>封装好的动画函数</li>
    <pre>
      donghua(obj, target, fun) {
      clearInterval(obj.timer);
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft == target) {
          clearInterval(obj.timer);
          if (fun) {
            fun();
          }
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 15);
    },
    </pre>
    <h3>任务分析</h3>
    <pre>
      1.点击左右侧按钮翻页
      2.图片翻页的同时小圆圈跟随变化
      3.点击小圆圈播放对应图片
      4.鼠标不经过时自动定时翻页

      //先做3了
    </pre>
    <h3>3.1根据图片个数动态生成小圆圈</h3>
    <pre>
      //获取轮播图数量
      var content = document.querySelector(".content");
      var lunbo = content.querySelectorAll("div").length;
      //获取轮播按钮小点的父元素（添加节点需要）
      var list = document.querySelector(".list");
      //循环创建带有类名的li插入到list
      for (var i = 0; i < lunbo; i++) {
        //创建li元素
        var addli = document.createElement("li");
        //给li元素一个class名
        addli.className = "li";
        //把li添加到小点的父元素上
        list.appendChild(addli);
      }
    </pre>
    <h3>3.2小圆圈排他思想</h3>
    <pre>
      //默认给第一个li添加选中类名
      document.querySelector(".list").children[0].className = "dangqian";
      //给所有li添加点击事件排他类名，当前选中
      for (var i = 0; i < list.children.length; i++) {
        //给每一个li绑定点击事件
        list.children[i].onclick = function () {
          //先干掉其他人的类名
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //自己变白色
          this.className = "dangqian";
        };
      }
    </pre>
    <h3>3.3点击小圆圈滚动图片</h3>
    <pre>
      //首先 在第一步循环添加小圆圈中 加入一个自定义属性index用来和图片绑定
      addli.className = "li";
      addli.setAttribute("index", i); //&lt;-------这是给1.3定义的属性index
      //把li添加到小点的父元素上
      list.appendChild(addli);


      //给所有li绑定点击事件内获取自定义属性index并调用动画函数
      list.children[i].onclick = function () {
          //先干掉其他人的类名
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //自己变白色
          this.className = "dangqian";

          //1.3点击小圆圈滚动图片
          //获取自定义的index属性
          var index = this.getAttribute("index");
          //调用封装好的动画函数
          donghua(content, index * -content.children[index].offsetWidth);
        };
    </pre>
    <h3>1.1右侧按钮无限滚动</h3>
    <pre>
      var ul = document.querySelector(".content");
      var liWidth = ul.children[0].offsetWidth;
      //外面先声明变量 num

      //右键按钮点击事件内操作
      num++; //点击后num++
      donghua(ul, -num * liWidth);//调用动画
    </pre>
    <h3>1.2无缝滚动</h3>
    <pre>
      //在点击事件的 num++; 之前判断
      //判断是不是最后一张图片，如果是，直接跳到第一张
      if (num == ul.children.length - 1) {
        ul.style.left = 0;
        num = 0;
      }
      //这个时候滚动还有bug 只有粉蓝两个颜色正常滚动 因为需要给最后添加一张图片用来判断
    </pre>
    <h3>1.3克隆第一张图(用于无缝滚动)</h3>
    <pre>
       //1.6 克隆第一张图片
      var cont = document.querySelector(".content"); //获取父元素(用于添加)
      var first = cont.children[0].cloneNode(true); //true深度拷贝
      document.querySelector(".content").appendChild(first); //添加进去了
      //由于添加图片是在动态生成小圆圈和绑定事件后,所有并不会对页面有影响
    </pre>
    <h3>2.1圆圈和按钮同步变化</h3>
    <pre>
       var ol = document.querySelector(".list");
      //声明全局变量 dian = 0 用来控制小圆圈的变化

      //点击事件中使用dian 控制圆圈变化
      dian++ //点一次自增一次
      //判断小圆圈是否到底了，如果到底了和之前轮播一样，跳到第一个状态
      if (dian == ol.children.length) {
        dian = 0;
      }
      //小圆圈排他想象 变样式
       for (var i = 0; i < ol.children.length; i++) {
        ol.children[i].className = "li";
      }
      //自己变白色
      ol.children[dian].className = "dangqian";
    </pre>
    <h3>2.2修改两个bug</h3>
    <ol>
      <li>点击小圆圈虽然改变图片但是用来判断图片的变量num没有变</li>
      <li>小圆点和图片也要绑定关系 用来判断小圆点的变量dian要变</li>
    </ol>
    <pre>
      //1.解决方法 在点击小圆圈中把index也赋值给num 用来判断图片
      //在给小圆圈绑定点击事件时

      //获取自定义的index属性
      var index = this.getAttribute("index");
      num = index; //2.2修改小bug

      //2.解决方法 在点击小圆点事件中吧index也给dian就行了
      //获取自定义的index属性
      var index = this.getAttribute("index");
      num = index; //2.2修改小bug
      dian = index;//2.2修改小bug
    </pre>
    <h3>1.4实现左侧按钮点击事件</h3>
    <pre>
      var ul = document.querySelector(".content");
      var liWidth = ul.children[0].offsetWidth;
      var ol = document.querySelector(".list");
      if (num == 0) {
        num = ul.children.length - 1;//变成等于最后一个了
        ul.style.left = -num * liWidth + "px";//变成等于最后一个宽度了
      }
      num--;//变成num--了
      donghua(ul, -num * liWidth);
      dian--;//变成dian--了
      if (dian < 0) {//变成小于0的时候
        dian = ol.children.length - 1;//变成等于最后一个
      }
      for (var i = 0; i < ol.children.length; i++) {
        ol.children[i].className = "li";
      }
      ol.children[dian].className = "dangqian";
    </pre>
    <h3>4.1自动轮播</h3>
    <pre>
      var timer = setInterval(function () {
        //先document获取右键元素
        //然后直接右键.click() 就能调用右键的点击事件了
      }, 2000);

      //给content添加输入移入mouseenter移出mouseleave事件
      //移入清除定时器
       clearInterval(timer);
      timer = null;

      //移除启动定时器
      var timer = setInterval(function () {
        //先document获取右键元素
        //然后直接右键.click() 就能调用右键的点击事件了
      }, 2000);
    </pre>
    <h3>解决小bug-防止连续快速切换</h3>
    <pre>
      //利用自定义一个变量 true 或者false来控制是否动画
      //最外面声明flag 左右键点击事件中判断flag是true才执行全部事件

      //轮播图左右键点击事件中写入
       if (flag) {
        flag = false; //关闭节流阀
        //里面是点击事件需要执行的代码
        //调用动画时候后写入回调函数
        donghua(ul, -num * liWidth, function () {
          flag = true; //开启节流阀
        });
        }
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 0,
      dian: 0,
      flag: true,
      //
      html: `
    <!-- 轮播图盒子 -->
    <div class="lunbo">
      <!-- 装着图片的盒子 -->
      <div class="content">
        <div class="div1"></div>
        <div class="div2"></div>
        <div class="div3"></div>
      </div>
      <!-- 左右按钮 -->
      <div class="left">&lt;</div>
      <div class="right">&gt;</div>
      <!-- 小圆点的盒子 -->
      <div>
        <!-- ul里会动态添加li -->
        <ul class="list"></ul>
      </div>
    </div>`,
      css: `
  //使用了预编译器
  .lunbo {
  position: relative;
  width: 800px;
  height: 500px;
  overflow: hidden;
  background-color: rgba(255, 192, 203, 0.4);
  margin: 0 auto;
  .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 3500px;
    height: 100%;
    div[class^="div"] {
      width: 800px;
      height: 500px;

      float: left;
      &:nth-child(1) {
        background-color: red;
      }
      &:nth-child(2) {
        background-color: pink;
      }
      &:nth-child(3) {
        background-color: blue;
      }
      &:nth-child(4) {
        background-color: red;
      }
    }
  }
  .left,
  .right {
    cursor: pointer;
    display: flex;
    display: none;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 80px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .left {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .right {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
  .list {
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
    .li {
      list-style: none;
      width: 6px;
      height: 6px;
      border-radius: 3px;
      background-color: #ccc;
      margin-right: 10px;
    }
  }
  &:hover .left {
    display: flex;
  }
  &:hover .right {
    display: flex;
  }
}

.li {
  list-style: none;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #ccc;
  margin-right: 10px;
}
.dangqian {
  list-style: none;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  margin-right: 10px;
  background-color: #000;
}`,
    };
  },

  methods: {
    jinru() {
      clearInterval(this.timer);
      this.timer = null;
    },
    chuqu() {
      var that = this;
      this.timer = setInterval(function () {
        that.you();
      }, 2000);
    },
    donghua(obj, target, fun) {
      clearInterval(obj.timer);
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft == target) {
          clearInterval(obj.timer);
          if (fun) {
            fun();
          }
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 15);
    },
    you() {
      if (this.flag) {
        this.flag = false; //关闭节流阀
        var ul = document.querySelector(".content");
        var liWidth = ul.children[0].offsetWidth;
        var ol = document.querySelector(".list");
        if (this.num == ul.children.length - 1) {
          ul.style.left = 0;
          this.num = 0;
        }
        this.num++;
        var that = this;
        this.donghua(ul, -this.num * liWidth, function () {
          that.flag = true; //打开节流阀
        });
        this.dian++;
        if (this.dian == ol.children.length) {
          this.dian = 0;
        }
        for (var i = 0; i < ol.children.length; i++) {
          ol.children[i].className = "li";
        }
        //自己变白色
        ol.children[this.dian].className = "dangqian";
        //this.className = "dangqian";
      }
    },
    zuo() {
      if (this.flag) {
        this.flag = false; //关闭节流阀
        var ul = document.querySelector(".content");
        var liWidth = ul.children[0].offsetWidth;
        var ol = document.querySelector(".list");
        if (this.num == 0) {
          this.num = ul.children.length - 1; //变成等于最后一个了
          ul.style.left = -this.num * liWidth + "px"; //变成等于最后一个宽度了
        }
        this.num--; //变成num--了
        var that = this;
        this.donghua(ul, -this.num * liWidth, function () {
          that.flag = true;
        });
        this.dian--; //变成dian--了
        if (this.dian < 0) {
          //变成小于0的时候
          this.dian = ol.children.length - 1; //变成等于最后一个
        }
        for (var i = 0; i < ol.children.length; i++) {
          ol.children[i].className = "li";
        }
        //自己变白色
        ol.children[this.dian].className = "dangqian";
        //this.className = "dangqian";
      }
    },

    //案例
    main() {
      //1.1根据图片个书动态生成小圆圈
      //获取轮播图数量
      var content = document.querySelector(".content");
      var lunbo = content.querySelectorAll("div").length;
      //获取轮播按钮小点的父元素（添加节点需要）
      var list = document.querySelector(".list");
      //循环创建带有类名的li插入到list
      for (var i = 0; i < lunbo; i++) {
        //创建li元素
        var addli = document.createElement("li");
        //给li元素一个class名
        addli.className = "li";
        addli.setAttribute("index", i); //这是给1.3定义的属性index
        //把li添加到小点的父元素上
        list.appendChild(addli);
      }

      //1.2小圆圈排他思想
      //默认给第一个li添加选中类名
      document.querySelector(".list").children[0].className = "dangqian";
      //给所有li添加点击事件排他类名，当前选中
      for (var i = 0; i < list.children.length; i++) {
        //给每一个li绑定点击事件
        var that = this;
        list.children[i].onclick = function () {
          //先干掉其他人的类名
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //自己变白色
          this.className = "dangqian";
          //1.3点击小圆圈滚动图片
          //获取自定义的index属性
          var index = this.getAttribute("index");
          this.num = index; //2.2修改小bug
          this.dian = index; //2.2修改小bug
          //调用封装好的动画函数
          that.donghua(content, index * -content.children[index].offsetWidth);
        };
      }

      //1.3点击小圆圈滚动图片 完成

      //1.4右侧按钮无限滚动 完成

      //1.5无缝滚动 完成

      //1.6 克隆第一张图片
      var cont = document.querySelector(".content"); //获取父元素(用于添加)
      var first = cont.children[0].cloneNode(true); //true深度拷贝
      document.querySelector(".content").appendChild(first); //添加进去了
      //由于添加图片是在动态生成小圆圈和绑定事件后,所有并不会对页面有影响

      //4 自动轮播
      //自动播放
      var that = this;
      this.timer = setInterval(function () {
        that.you();
      }, 2000);
    },
  },
  computed: {},
  mounted() {
    this.main();
  },
  /*  mounted() {
    //获取轮播图数量
    var content = document.querySelector(".content");
    var lunbo = document
      .querySelector(".content")
      .querySelectorAll("div").length;
    //获取轮播按钮小点的父元素
    var list = document.querySelector(".list");
    //循环创建带有类名的li插入到list
    for (var i = 0; i < lunbo; i++) {
      var addli = document.createElement("li");
      addli.className = "li";
      addli.setAttribute("index", i);

      list.appendChild(addli);
    }
    //给第一个li添加选中类名
    document.querySelector(".list").children[0].className = "dangqian";
    //给所有li添加点击事件排他类名，当前选中
    for (var i = 0; i < list.children.length; i++) {
      //给每一个li绑定点击事件
      var that = this;
      list.children[i].onclick = function () {
        //先把其他人变白
        for (var i = 0; i < list.children.length; i++) {
          list.children[i].className = "li";
        }
        //自己变白色
        this.className = "dangqian";
        var index = this.getAttribute("index");
        that.num = index;
        that.dian = index;
        console.log(-content.children[index].offsetWidth);
        that.donghua(content, index * -content.children[index].offsetWidth);
      };
    }
    //克隆图片
    var first = document.querySelector(".content").children[0].cloneNode(true);
    document.querySelector(".content").appendChild(first);

    //自动播放
    var that = this;
    this.timer = setInterval(function () {
      that.you();
    }, 2000);
  }, */
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

<style lang="less" scoped>
