<template>
  <div class="Animatior">
    <h3>Animation动画封装</h3>
    <div class="donghua" v-show="flg"></div>
    <pre>
      function donghua(obj, target) {
        var timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(timer);
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      donghua(box, 600);
      <button @click="fengzhuang">点我试试</button> </pre>
    <h3>优化动画中的定时器</h3>
    <div class="donghua2"></div>
    <pre>
      //由于封装后调用对象太多的话定时器名字重复不太友好
      //可以把传入的对象中新建属性当做定时器名字

        function donghua(obj, target) {
        obj.timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(obj.timer); //关闭之前的定时器
      donghua(box, 600);
      <button @click="youhua">点我试试</button></pre>
    <h3>动画速度改变</h3>
    <div class="biansu"></div>
    <pre>
      //匀速动画 盒子当前位置 + 固定值 10
      //缓速动画 盒子当前位置 + 变化的值-->（目标值 - 现在的位置）/10
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //定时器内写入动画速度公式
          var step = (target - obj.offsetLeft) / 10;
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //关闭之前的定时器
      donghua(box, 600);
        <button @click="biansu">点我试试</button></pre>
    <h3>优化距离取整</h3>
    <li>Math.ceil() (进一位)</li>
    <li>Math.floor() (退一位)</li>
    <pre>
        function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //定时器内写入动画速度公式
          //取整
          var step = (target - obj.offsetLeft) / 10;
          step > 0 ?Math.ceil(step):Math.floor(step)
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //关闭之前的定时器
      donghua(box, 600);
        </pre
    >
    <h3>添加回调函数</h3>
    <pre>
      function donghua(obj, target，fun) {
        obj.timer = setInterval(function () {
          var step = (target - obj.offsetLeft) / 10;
          step > 0 ?Math.ceil(step):Math.floor(step)
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            fun();//事件结束调用
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); 
      donghua(box, 600，function(){});//传递函数进去
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flg: false,
    };
  },
  methods: {
    fengzhuang() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        var timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      donghua(box, 600);
    },
    youhua() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //关闭之前的定时器
      donghua(box, 600);
    },
    biansu() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //定时器内写入动画速度公式
          var step = (target - obj.offsetLeft) / 10;
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //关闭之前的定时器
      donghua(box, 600);
    },
    donghua(obj, target, fun) {
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft >= target) {
          clearInterval(obj.timer);
          fun();
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 30);
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style>
.donghua {
  width: 100px;
  height: 100px;
  background-color: pink;
  position: absolute;
  top: 50px;
  left: 400px;
}
.ceshi {
  width: 200px;
  height: 40px;
  line-height: 40px;
  text-align: left;
  background-color: pink;
  position: absolute;
  top: 50px;
  left: 400px;
}
</style>