<template>
  <div class="Duilie">
    <h3>js执行队列</h3>
    <ul>
      <li>js中程序的执行都是从上往下 等待上一个代码完成后下一个代码才会执行</li>
      <li>这样会造成拥堵 上面的代码等待时间比较长 下面的代码没办法执行</li>
      <li></li>
      <li>为了解决这个问题 现在代码已经提出了异步思想</li>
      <li></li>
      <li>代码从上往下依次执行 --同步</li>
      <li>
        代码从上往下,时间较长的代码另外开辟空间去执行,不影响下面代码 --异步
      </li>
    </ul>
    <hr>
    <hr>
    <ul>
      <li>js会把有回调的任务当成异步任务,放到另一个空间去执行</li>
      <li>常见的异步任务</li>
      <li>click等鼠标键盘事件  定时器  资源加载的事件 .....</li>
    </ul>
  </div>
</template>

